.App {
  text-align: center;
}

* {
  box-sizing: border-box;
  font-family: Arial, Helvetica, sans-serif;
  background-color: #ddd;
}

.content {
  background-color: #ddd;
  padding: 10px;
  text-align: center;
}

.footer {
  background-color: #f1f1f1;
  padding: 10px;
  text-align: end;
}

.footer a, .footer p {
  background-color: #f1f1f1;
}

.topnav {
  overflow: hidden;
  background-color: #333;
}

.topnav a {
  float: right;
  display: block;
  background-color: #333;
  color: #f2f2f2;
  text-align: center;
  padding: 14px 16px;
  margin-right: 50px;
  text-decoration: none;
}

.topnav a:hover {
  background-color: #ddd;
  color: black;
}

.column {
  float: left;
  padding: 10px;
}

.column.qtr {
  width: 25%;
  height: fit-content;
}

.column.hlf {
  width: 50%;
  height: fit-content;
}

.column.thrd {
  width: 33%;
  height: fit-content;
}

.column.twthrd {
  width: 66%;
  height: fit-content;
}

.column.hlf h2, .column.qtr h2, .column.thrd h2, .column.twthrd h2 {
  text-decoration: underline;
}

.row::after {
  content: "";
  display: table;
  clear: both;
}

.pdf {
  width: 100%;
  height: 1150px;
  margin: 0;
  padding: 0;
}

.contactform {
  text-align: center;
}

.demobtn {
  margin-top: 100px;
}

.demobtn a {
  text-decoration: none;
  color: #333;
  padding: 15px;
  border: 2px solid black;
  border-radius: 50px;
}

.demobtn a:hover {
  color: white;
  background-color: red;
}

.projnav button {
  padding: 15px;
  margin: 10px 50px;
  border-radius: 5px;
}

.projnav button:hover {
  color: white;
  background-color: blue;
}

/* Responsive layout */
@media screen and (max-width: 600px) {
  .column.qtr, .column.hlf, .column.thrd, .column.twthrd {
    width: 100%;
  }
}